export default {
	data() {
		return {
			form: {
				truename: "",
				phone: ""
			}
		}
	},
	methods: {
		handleAdd() {
			if(!this.form.truename) {
				uni.showModal({
					content: "请输入真实姓名",
					showCancel: false
				});
				return false;
			}
			if(!this.form.phone) {
				uni.showModal({
					content: "请输入手机号",
					showCancel: false
				});
				return false;
			}
			this.$request({
				url: '/api/wx/authenticate',
				data: {
					truename: this.form.truename,
					phone: this.form.phone,
					openId: JSON.parse(uni.getStorageSync("userInfo")).openId
				}
			}).then(res=>{
				if(res.code===this.IS_OK){
					uni.navigateTo({
						url: `/pages/index/index`
					});
				} else {
					uni.showModal({
					    title: '温馨提示',
					    content: '用户不存在，请联系管理员',
					    success: function (res) {
					        if (res.confirm) {
					            console.log('用户点击确定');
					        } else if (res.cancel) {
					            console.log('用户点击取消');
					        }
					    }
					});
				}
			})
		}
	}
}
